import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/d/:id",
    name: "dice",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Dice.vue"),
    props: true,
  },
  {
    path: "/r/:id",
    name: "gamble",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Rock.vue"),
    props: true,
  },
  {
    path: "/k/:id",
    name: "KingJoker",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/KingJoker.vue"),
    props: true,
  },
  {
    path: "/g/:id",
    name: "gamble",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Room.vue"),
    props: true,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
